/* Header Styles */
.header {
  background: linear-gradient(135deg, #0f3460 0%, #1a1a2e 100%);
  border-bottom: none;
  position: sticky;
  top: 0;
  z-index: 100;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  padding: 0.6rem 1.5rem;
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 100%;
  gap: 1.5rem;
}

.header .brand {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1.2rem;
  font-weight: 700;
  background: linear-gradient(135deg, #00d4ff 0%, #00a8cc 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  min-width: fit-content;
  text-decoration: none;
}

.header .brand-logo {
  width: 35px;
  height: 35px;
  border-radius: 6px;
  object-fit: cover;
  box-shadow: 0 2px 6px rgba(0, 212, 255, 0.3);
}

.header .nav {
  flex: 1;
}

.header .nav ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 0.5rem;
  align-items: center;
}

.header .nav li {
  min-width: fit-content;
}

.header .nav a {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.4rem;
  padding: 0.45rem 0.8rem;
  color: #1a1a2e;
  text-decoration: none;
  border-radius: 8px;
  transition: all 0.3s ease;
  white-space: nowrap;
  font-size: 0.8rem;
  font-weight: 600;
  background: linear-gradient(135deg, rgba(100, 200, 255, 0.15) 0%, rgba(50, 150, 255, 0.1) 100%);
  border: 1.5px solid rgba(100, 200, 255, 0.4);
  box-shadow: 0 2px 8px rgba(0, 212, 255, 0.1);
  color: #e0e0e0;
}

.header .nav a:hover {
  background: linear-gradient(135deg, rgba(100, 200, 255, 0.25) 0%, rgba(50, 150, 255, 0.2) 100%);
  color: #00d4ff;
  border-color: #00d4ff;
  box-shadow: 0 4px 16px rgba(0, 212, 255, 0.3);
  transform: translateY(-2px);
}

.header .nav li.active a {
  background: linear-gradient(135deg, #00d4ff 0%, #00a8cc 100%);
  color: #1a1a2e;
  border-color: #00d4ff;
  font-weight: 700;
  box-shadow: 0 4px 16px rgba(0, 212, 255, 0.4);
}

.header .nav .icon {
  font-size: 0.95rem;
}

.header .nav .text {
  display: inline;
}

.header .user-info {
  color: #e0e0e0;
  font-size: 0.8rem;
  min-width: fit-content;
  padding: 0.45rem 0.8rem;
  background-color: transparent;
  border-radius: 8px;
  border: 2px solid #00d4ff;
  font-weight: 600;
}

.header .user-info strong {
  color: #00d4ff;
  font-weight: 700;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .header-content {
    padding: 0.75rem 1.5rem;
    gap: 1.5rem;
  }

  .header .nav a {
    padding: 0.5rem 0.95rem;
    font-size: 0.85rem;
  }

  .header .nav .icon {
    font-size: 1rem;
  }
}

@media (max-width: 768px) {
  .header-content {
    flex-wrap: wrap;
    padding: 0.75rem 1rem;
    gap: 1rem;
  }

  .header .brand {
    order: 1;
  }

  .header .user-info {
    order: 3;
    flex: 1 100%;
    text-align: center;
  }

  .header .nav {
    order: 2;
    flex: 1 100%;
  }

  .header .nav ul {
    justify-content: center;
    flex-wrap: wrap;
  }

  .header .nav a {
    padding: 0.5rem 0.85rem;
    font-size: 0.8rem;
  }

  .header .nav .icon {
    font-size: 0.95rem;
  }

  .header .nav .text {
    display: none;
  }
}

@media (max-width: 480px) {
  .header-content {
    padding: 0.5rem 0.75rem;
    gap: 0.5rem;
  }

  .header .brand {
    gap: 0.5rem;
    font-size: 1.2rem;
  }

  .header .brand-logo {
    width: 32px;
    height: 32px;
  }

  .header .nav ul {
    gap: 0.25rem;
  }

  .header .nav a {
    padding: 0.45rem 0.7rem;
    gap: 0.3rem;
  }

  .header .nav .icon {
    font-size: 0.9rem;
  }

  .header .user-info {
    font-size: 0.8rem;
    padding: 0.5rem 0.85rem;
  }
}
